@import url("https://fonts.googleapis.com/css?family=Poppins");
.Previous-Bootcamp {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  width: 100%;
  height: 100%;
}
.previousbootcamp-section {
  display: flex;
  width: 100%;
}
.detailcourses-banner {
  display: flex;
  flex-direction: row;
  width: 90%;
  height: 600px;
  margin: auto;
  border-radius: 20px;
  background-image: url("../../assets/detail-course.jpeg");
  object-fit: contain;
  align-items: flex-end;
  justify-content: flex-end;
  position: relative;
}
.foot-banner {
  background-repeat: no-repeat;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100px;
  margin: auto;
  border-radius: 15px;
  background-image: url("../../assets/bannerimg.png");
  object-fit: contain;
  align-items: center;
  justify-content: space-between;
  position: absolute;
}
.footbanner-text {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #ffffff;
  padding: 10px;
}
.detailcourses-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: auto;
  background-color: #ffffff;
  /* border-radius: 20px; */
  padding-bottom: 10px;
  position: relative;
}
.bootcamp-wrapper {
  display: flex;
  flex-direction: row;
  width: 96%;
  align-items: flex-start;
  justify-content: center;
}
.wrapper-left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 70%;
}
.wrapper-right {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 30%;
}
/* Training program */
.training-program {
  margin-top: 20px;
  margin-left: 20px;
}
.program-heading {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #243a73;
}
.program-goal {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 28px;
  color: rgba(0, 0, 0, 0.4);
}
.costing-card {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 50px;
  margin-bottom: 50px;
}
@media only screen and (max-width: 600px) {
  .training-program {
    margin: auto;
    width: 100%;
  }
  .program-heading{
    margin: 10px auto;
  }
  .detailcourses-banner {
    display: none;
  }
  .foot-banner {
    display: none;
  }
  .bootcamp-wrapper {
    display: flex;
    flex-direction: column !important;
    width: 100%;
  }
  .detailcourses-container{
    margin-top: 20px;
  }
}
/* Costing Card*/
.program-cost {
  display: flex;
  flex-direction: column;
  width: 33%;
}
.head1 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 54px;
  color: #243a73;
  display: flex;
}
.head2 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.3);
}
.program-enroll {
  display: flex;
  align-items: center;
  justify-content: center;
  /* width: 33%; */
}
.enrollBtn {
  width: 202px;
  height: 67px;
  background-color: #243a73;
  box-shadow: 0px 6px 20px 2px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  color: #ffffff;
  outline: none;
  border: none;
  cursor: pointer;
}
.syllabus-download {
  display: flex;
  align-items: center;
  justify-content: center;
}
.downloadBtn {
  width: 285px;
  height: 67px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  color: #243a73;
  background: #ffffff;
  border: 1px solid #243a73;
  border-radius: 5px;
  cursor: pointer;
}
@media only screen and (max-width: 600px) {
  .costing-card {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
  }
  .program-cost {
    width: 100%;
    margin: 10px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .program-enroll {
    width: 100%;
    margin: 10px auto;
  }
  .enrollBtn {
    width: 285px;
    height: 67px;
  }
  .syllabus-download {
    width: 100%;
    margin: 10px auto;
  }
}
/* Benefit card */
.benefitCard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 33%;
  height: 325px;
  background-color: #f5f5f5;
  margin: 20px 10px;
}
.card-benefit {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  color: #000000;
}
.benefit-description {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 22px;
  text-align: center;
  color: rgba(0, 0, 0, 0.46);
  margin: 5px auto;
  width: 96%;
}
.benefit-card {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: #ffffff;
}
.instructor-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 40px auto;
  background-color: #f6f7ff;
}
.section-heading {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  line-height: 48px;
  color: #243a73;
  margin: 20px 0px;
}
.flex-start {
  justify-content: flex-start;
}
.section-heading-ext {
  justify-content: flex-start !important;
  margin: 50px 30px;
}
.card-section {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  justify-content: center;
  overflow-x: scroll;
  overflow-y: hidden;
}
.card-section::-webkit-scrollbar {
  display: none;
}
@media only screen and (max-width: 600px) {
  .benefit-card {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .benefitCard {
    width: 100%;
    margin: 10px auto;
    display: flex;
    align-items: center;
    justify-self: center;
  }
}
/* Instructor Card */
.instructorCard {
  min-width: 250px;
  height: 333px;
  background: #ffffff;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px;
}
.instructorImg {
  display: flex;
  align-items: center;
  justify-content: center;
}
.instructorName {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: #243a73;
}
.instructorExp {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.46);
}
.instructorCompany {
  display: flex;
  align-items: center;
  justify-content: center;
}
.carousel-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.carousel-logo {
  margin: 5px;
  width: 50px;
}
.tech-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 40px auto;
}
.tech-heading {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  line-height: 48px;
  color: #243a73;
  width: 100%;
  text-align: center;
}
.tech-logo {
  margin: 20px;
  width: 90px;
  height: 90px;
}
.techcard-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 20px;
  overflow-x: scroll;
  overflow-y: hidden;
}
.techcard-section::-webkit-scrollbar {
  display: none;
}
.certificate-section {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 40px auto 40px auto;
}
.certificate-content {
  display: flex;
  flex-direction: column;
  width: 50%;
  align-items: flex-start;
  justify-content: flex-start;
  margin-left: 30px;
}
.certificate-name {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 55px;
  line-height: 96px;
  color: #243a73;
  width: 100%;
}
.certificate-info {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: rgba(0, 0, 0, 0.46);
  width: 90%;
}
.certificate-img {
  width: 50%;
  box-shadow: 10px 10px 30px 20px #f2f2f2;
  margin-left: 20px;
}
.certificate {
  width: 100%;
  height: 482px;
}
@media only screen and (max-width: 600px) {
  .techcard-section {
    justify-content: initial;
  }
  .card-section::-webkit-scrollbar,
  .techcard-section::-webkit-scrollbar {
    display: block;
  }
  .card-section {
    justify-content: initial;
  }
  .certificate-section {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    margin: 10px auto;
    width: 100%;
  }
  .certificate-img {
    width: 100%;
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px !important;
  }
  .certificate {
    width: 100% !important;
    height: 100% !important;
  }
  .certificate-content {
    display: flex;
    flex-direction: column;
    width: 90%;
    align-items: flex-start;
    justify-content: flex-start;
    margin: 30px !important;
  }
  .certificate-name {
    font-weight: 600;
    font-size: 28px;
    line-height: 30px;
  }
  .certificate-name br {
    display: none;
  }
  .certificate-info {
    margin: 10px 0px;
  }
}
/* Sidebar */
.sidebarHeaderLeft {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  line-height: 48px;
  color: #243a73;
  width: 70%;
}
.sidebarHeaderRight {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
}
/* Course Card */
.course-card-alt {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 80px;
  background: #ffffff;
  border-radius: 10px;
  margin: 10px 0px;
}
.course-name {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 30px;
  color: #243a73;
  padding: 5px 10px;
}
.course-duration {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.46);
  padding: 5px 10px;
}
.learn-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin: 10px;
  width: 100%;
  height: fit-content;
  padding-left: 20px;
}
.tickImg {
  display: flex;
  margin: 5px;
}
.learn-text {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: rgba(0, 0, 0, 0.46);
  width: 100%;
  margin: 10px;
}
.learn-section-alt {
  display: flex;
  flex-direction: column;
  margin: 30px auto;
  width: 100%;
}
.learning-card {
  width: 98%;
  display: grid;
  grid-template-columns: 50% 50%;
  align-items: center;
  justify-content: center;
  background-color: #f6f7ff;
  border-radius: 20px;
  height: fit-content;
}
@media only screen and (max-width: 600px) {
  .learning-card {
    display: flex;
    flex-direction: column;
    width: 100% !important;
    margin: 10px auto !important;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .course-name{
    font-size: 14px;
  }
  .learn-section {
    margin: 10px auto !important;
  }
  .learn-wrap {
    display: flex;
    justify-content: center !important;
    align-items: center !important;
    flex-direction: row;
    margin: 10px;
    width: 100% !important;
    height: fit-content;
  }
  .learn-text {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: rgba(0, 0, 0, 0.46);
    margin: 0px;
  }
  .sidebarHeaderLeft{
    font-size: 28px;
  }
}
/* Choose us */
.choose-us {
  display: flex;
  flex-direction: row;
  width: 100%;
  margin: 40px auto;
}
.chooseus-banner {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 50px;
}
.banner-image {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 497px;
  height: 609px;
  border-radius: 10px;
  background-image: url("../../assets/bluebg.png");
  object-fit: contain;
  align-items: center;
  justify-content: center;
}
.banner-head {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  line-height: 48px;
  text-align: center;
  color: #ffffff;
  padding: 10px;
}
.banner-subheading {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  padding: 10px;
}
.chooseus-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: fit-content;
}
.content-head {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 54px;
  text-align: center;
  color: #243a73;
}
@media only screen and (max-width: 600px) {
  .choose-us {
    margin: 10px auto;
    align-items: center;
    justify-content: center;
  }
  .chooseus-banner {
    display: none;
  }
  .chooseus-content {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .content-head {
    font-size: 36px;
    line-height: 40px;
    text-align: center;
    color: #243a73;
  }
}
/* Question Section*/
.question-section {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  width: 100%;
  margin: 40px auto;
}
.question-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.question-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 96%;
  height: fit-content;
  margin: 10px;
  background-color: #f6f7ff;
  border-radius: 11px;
}
.card-top {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 90%;
  padding: 20px;
}
.question {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  line-height: 30px;
  color: #243a73;
  width: 80%;
  cursor: pointer;
}
.logo {
  display: flex;
  align-items: center;
  justify-content: center;
}
.answer {
  display: none;
  align-items: flex-start;
  justify-content: center;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  padding-bottom: 20px;
  line-height: 30px;
  color: rgba(0, 0, 0, 0.46);
  text-align: justify;
  width: 90%;
}
@media only screen and (max-width: 600px) {
  .question-section {
    margin: 10px auto;
  }
  .question {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    color: #243a73;
  }
  .answer {
    display: none;
    align-items: flex-start;
    justify-content: center;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 30px;
    color: rgba(0, 0, 0, 0.46);
    text-align: justify;
    width: 90%;
  }
  .section-heading {
    font-size: 30px;
    margin: 10px !important;
  }
  .card-top {
    padding: 10px;
  }
}
