.header {
  display: flex;
  width: 100%;
  height: 100px;
  top: 0px;
  position: relative;
  z-index: 999;
  align-items: center;
  overflow: hidden;
  background-color: #fff;
  position: fixed;
  /* border-bottom: 0.25px solid rgba(0, 0, 0, 0.3); */
  /* box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.3); */
}
.header-ham {
  display: none;
}
.header-logo {
  object-fit: contain;
  height: 100%;
  width: 300px;
}
.header-L {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 40%;
  right: 0px;
  position: absolute;
}
.header-R {
  height: 100%;
  width: fit-content;
  overflow: hidden;
}
.header-option {
  color: #243a73;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 900;
  letter-spacing: 1px;
  cursor: pointer;
  width: 100px;
  text-align: center;
  background-color: transparent;
}
.selected {
  border: 0px none transparent;
  border-bottom: 3px solid #64a5ca;
}
.header-login {
  height: 40px;
  width: 120px;
  color: #fff;
  background-color: #243a73;
  border-radius: 5px;
  text-transform: uppercase;
  border: 0px none transparent;
  font-weight: 600;
  letter-spacing: 1px;
}
.header-search {
  width: 20px;
  height: 20px;
}
.menu-blur {
  display: none;
}
@media only screen and (max-width: 600px) {
  .footer {
    flex-direction: column;
    height: fit-content;
    width: 100%;
    padding-bottom: 20px;
  }
  .footer-section {
    width: fit-content;
    display: block;
  }
  .header {
    flex-direction: row;
    top: 0px;
    position: fixed;
    width: 100%;
    display: flex;
    height: 70px;
    align-items: center;
  }
  .header-L {
    display: none;
    position: relative;
    width: 100%;
  }
  .company-motto {
    line-height: normal;
  }
  .header-menu {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    width: 70%;
    height: 100%;
    position: fixed;
    right: 0px;
    top: 0px;
    z-index: 9999;
  }
  .header-option {
    height: 50px;
    width: 100%;
    margin: 10px !important;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .selected {
    background-color: #64a5ca;
    color: white;
  }
  .menu-blur {
    display: none;
    background-color: rgba(0, 0, 0, 0.325);
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
  }
  .header-ham {
    display: block;
    margin: auto 20px auto auto;
  }
  .ham-ico {
    height: 35px;
    width: 35px;
  }
  .header-logo {
    width: 180px;
  }
  .head-alt {
    margin: auto;
  }
}
